.profileScreen {
  height: 100vh;
  color: white;
}

.profileScreen__body {
  display: flex;
  flex-direction: column;
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 8%;
  max-width: 800px;
}

.profileScreen__body > h1 {
  font-size: 60px;
  font-weight: 400;
  border-bottom: 1px solid #282c2d;
  margin-bottom: 20px;
}

.profileScreen__info {
  display: flex;
}

.profileScreen__info > img {
  height: 100px;
}

.profileScreen__details {
  color: white;
  margin-left: 25px;
  flex: 1;
}

.profileScreen__details > h2 {
  background-color: rgb(255, 255, 255);
  color: #e50914;
  padding: 15px;
  border-radius: 10px;
  font-size: 15px;
  padding-left: 20px;
}

.profileScreen__plans {
  margin-top: 20px;
}

.profileScreen__plans > h3 {
  border-bottom: 1px solid #282c2d;
  padding-bottom: 10px;
}

.profileScreen__signOut {
  padding: 12px 20px;
  font-size: 1rem;
  margin-top: 5%;
  width: 100%;
  border-radius: 10px;
  color: white;
  background-color: #e50914;
  font-weight: 600;
  border: none;
  cursor: pointer;
}

@media screen and (max-width: 1366px) {
  .profileScreen__body {
    position: fixed;
  }
  .profileScreen__body > h1 {
    font-size: 2rem;
    margin-top: 40px;
    font-weight: 300;
  }
  .profileScreen__info {
    display: flex;
    flex-direction: row;
  }
}
